import React from 'react';
import './ShopCar.css'
import * as types from '../store/type'
import { connect } from 'react-redux';
import {NavLink} from 'react-router-dom'
import {asyncActionShopCar} from '../store/anyscAction';
import QueueAnim from 'rc-queue-anim';



class ShopCar extends React.Component{
    constructor(props){
        super();
    }
   render(){
       let {user} = this.props
       if(!user.shopCar){
         return null
       }
       let sum = 0;
       for(var i=0;i<user.shopCar.length;i++){
           sum+=user.shopCar[i].price*user.shopCar[i].number
       }
        return <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000">
        <div className='shopcar' key={1}>
        <div className="mban">
            <img src="/image/step1.jpg"/>
        </div>
        <div className="p_des_bg">
        <ul className="carList">
            {
                user.shopCar.map((item,index)=>(
                    <li key={item.goodId}>
                <table style={{width:'94%', margin:'2% auto',border:0}} cellSpacing="0" cellPadding="0">
                <tbody><tr style={{valign:'top'}}>
                    <td rowSpan="3" style={{width:'11.6rem'}}><div className="c_img fl"><img src={item.src} style={{width:'9.8rem',height:'9.8rem'}}/></div></td>
                    <td style={{position:'relative'}}>{item.name} 275ml
                        <div className="c_close" onClick={()=>{
                            this.props.add({id:item.goodId,type:"del"}) 
                            setTimeout(()=>{
                                this.props.changeUser(this.props.user)
                            },0)
                        }}><a ><img src="/image/close.png"/></a></div>
                    </td>
                </tr>
                <tr style={{valign:'top'}}>
                    <td>颜色：灰色 &nbsp; &nbsp; 型号：S</td>
                </tr>
                <tr>
                    <td>
                        <span className="fl" style={{marginRight:'5%'}}><font>￥{item.price}</font> &nbsp; &nbsp;{item.size}</span>
                        <div className="j_nums">
                            <input type="button" defaultValue="" onClick={()=>{
                                this.props.add({id:item.goodId,type:"0"})
                                this.props.changeUser(this.props.user)
                            }} className="n_btn_2"/> 
                            <span className="n_ipt" style={{lineHeight:"3.6rem"}}>{item.number}</span> 
                            <input type="button" onClick={()=>{
                                this.props.add({id:item.goodId,type:"1"});
                                this.props.changeUser(this.props.user)
                            }} defaultValue="" className="n_btn_1"/>
                               
                        </div>
                    </td>
                </tr>
                </tbody></table>
            </li>
        ))}
        </ul>		
        <table style={{width:'94%', margin:'2% auto',border:0}} cellSpacing="0" cellPadding="0">
        <tbody><tr style={{height:'5.8rem'}}>
            <td style={{fontFamily:'&#39;Microsoft YaHei&#39', borderBottom:0, fontSize:'1.3em'}}>
                <label className="r_rad"><input type="checkbox" name="clear"/></label><label className="r_txt" onClick={()=>{
                    this.props.clearAll()
                    setTimeout(()=>{
                        this.props.changeUser(this.props.user)
                    },0)
                }}>清空购物车</label>
                <span className="fr">商品总价：<b style={{fontSize:'1.8rem', color:'#ff4e00'}}>￥ {sum}</b></span>
            </td>
        </tr>
        <tr style={{height:'12.5rem',valign:'top'}}>
            <td style={{textAlign:'left'}}>
                <a to="/product" className="fr" style={{marginTop:'2%',marginLeft:'2%'}}><img src="/image/buy2.gif"/></a> 
                <NavLink to="/product" className="fr" style={{marginTop:'2%'}}><img src="/image/buy1.gif"/></NavLink>
            </td>
        </tr>
        </tbody></table>
    </div>
    </div>
    </QueueAnim>
   }
}

let MapStateToProps = (state,props)=>({
    user:state.user
})

let MapDispatchToProps = (dispatch)=>({
    add:(data)=>{
        dispatch({type:types.UPDATE_SHOPCAR_NUM,payload:data})
    },
    clearAll:()=>{
        dispatch({type:types.CLEAR_SHOPCAR})
    },
    changeUser:(opt)=>{asyncActionShopCar(
        opt,
        dispatch,
        types.UPDATE_USER
    )}
})

export default connect(
    MapStateToProps,
    MapDispatchToProps
)(ShopCar)